import { Link } from "react-router-dom";
import { FavoriteButton } from "../elements/FavoriteButton";

// CSS
import '../../assets/css/containers/jobs-list.css';

//Icons
import IcLocation from '../../assets/images/icons/ic-location.svg';
import IcSuitcase from '../../assets/images/icons/ic-suitcase.svg';
import IcTime from '../../assets/images/icons/ic-time.svg';

// Images
import employerLogo1 from '../../assets/images/employer-3.png';

function JobsList() {
    return (
        <div className="jobs-list">
            <ul>
                {
                    [...Array(6)].map((item, idx) => {
                        return (
                            <li key={idx}>
                                <div className="indi-job-wrapper">
                                    <div className='emloyer-info'>
                                        <div className="employer-logo">
                                            <img src={employerLogo1} alt="" />
                                        </div>
                                        <div className='job-info'>
                                            <span className='job-position'>SEO Mananger</span>
                                            <div className="job-metas">
                                                <div className="job-employer">Wonka Industries</div>
                                                <div className="job-location with-icon"><img src={IcLocation} alt="" /> Baghdad, Iraq</div>
                                                <div className="job-type with-icon"><img src={IcSuitcase} alt="" />Full Time</div>
                                            </div>
                                        </div>
                                    </div>
                                    <p className='job-desc'>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam hendrerit congue tortor nunc tellus imperdiet.
                                        Aenean quis quis cras urna turpis sagittis, hendrerit. Amet in interdum quisque. </p>
                                    <ul className='job-desc-list'>
                                        <li>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Purus commodo sit quis aliquet elementum.</li>
                                    </ul>

                                    <div className="job-info-bottom clearfix">
                                        <div className="job-types float-md-start">
                                            <Link className="type-job" to="/">Senior</Link>
                                            <Link className="urjent" to="/">Urgent Hiring</Link>
                                        </div>
                                        <div className="job-metas float-md-end">
                                            <div className="job-time with-icon m-0"><img src={IcTime} alt="" /> 10 min ago</div>
                                        </div>
                                    </div>
                                    
                                    <FavoriteButton/>
                                </div>
                            </li>
                        )
                    })
                }
            </ul>
        </div>
    )
}

export { JobsList }