

import Slider from 'rc-slider/lib/Slider';

import 'rc-slider/assets/index.css';

//CSS

import '../assets/css/pages/job-listing.css';


// 
import { JobsList } from "../component/containers/JobsList";

function JobListing() {
    return (
        <div className="main-content">
            <section className="job-listing">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>Job Listing</h4>
                    </div>
                    <div className="row flex-lg-row-reverse">
                        <div className="col-lg-9">
                            <JobsList />
                        </div>
                        <div className="col-lg-3">
                            <aside className="job-filter">
                                <form action="">
                                    <span className="filter-title">Location</span>
                                    <div className='form-group'>
                                        <input type="text" className='form-control' id='location' placeholder='Enter Location' />
                                    </div>
                                    <div className='salary-range'>
                                        <span className="filter-title">Salary Range</span>
                                        <Slider range allowCross={false} defaultValue={[20, 40]} />
                                    </div>
                                    <div className='filter'>
                                        <span className='filter-title'>Search By</span>
                                        <div className="filter-list">
                                            <ul>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_1' />
                                                        <label htmlFor="job_type_1">Full Time Jobs</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_2' />
                                                        <label htmlFor="job_type_2">Part Time</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_3' />
                                                        <label htmlFor="job_type_3">Remote jobs</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_4' />
                                                        <label htmlFor="job_type_4">Internship</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_5' />
                                                        <label htmlFor="job_type_5">Contract</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_6' />
                                                        <label htmlFor="job_type_6">Freelance</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='job-type' id='job_type_7' />
                                                        <label htmlFor="job_type_7">Temporary</label>
                                                    </div>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div className='filter'>
                                        <span className='filter-title'>Experience Level</span>
                                        <div className="filter-list">
                                            <ul>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='experience-level' id='experience_level_1' />
                                                        <label htmlFor="experience_level_1">Full Time Jobs</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='experience-level' id='experience_level_2' />
                                                        <label htmlFor="experience_level_2">Expert</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='experience-level' id='experience_level_3' />
                                                        <label htmlFor="experience_level_3">Senior</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='experience-level' id='experience_level_4' />
                                                        <label htmlFor="experience_level_4">Junior</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='experience-level' id='experience_level_5' />
                                                        <label htmlFor="experience_level_5">Internship</label>
                                                    </div>
                                                </li>
                                                <li>
                                                    <div className='theme-radio'>
                                                        <input type="radio" name='experience-level' id='experience_level_6' />
                                                        <label htmlFor="experience_level_6">Associate</label>
                                                    </div>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>

                                    <button className='button button-blue w-100'>Apply</button>
                                </form>
                            </aside>
                        </div>

                    </div>
                </div>
            </section>
        </div>

    )
}

export default JobListing;